.guidance{
	&-bottom{
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		width: 100%;
		height: 60px;
		display: flex;
		align-items: center;
		background: #000;
		z-index: 9991;
		transition: all 0.3s ease-in-out;
		&.hide{
			transform: translateY(60px);
			opacity: 0;
		}
		&__close{
			color: white;
		    padding: 4px;
		}
		&__logo{
			width: 40px;
			height: 40px;
			margin: 0 7px 0 2px;
			background-image: url('../images/fun.png');
		}
		&__tips{
			flex: 1;
			width: 120px;
			padding-top: 30px;
			background-image: url('../images/zhufan-down.png');
			background-size: 120px 27px;
			background-position: left top;
			color: white;
			font-size: 12px;
		}
		&__download{
			width: 100px !important;
			height: 30px !important;
			line-height: 32px !important;
			border-radius: 30px !important;
			font-size: 14px !important;
			margin-right: 10px;
		}
	}
	&-shade{
		position: fixed;
		left: 0;
    	top: 0;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,.5);
		z-index: 9919998;
	}
	&-alert{
		position: fixed;
		left: 0;
    	top: 0;
		width: 100%;
		height: 100%;
		// background: rgba(0,0,0,.5);
		z-index: 9919999;
		&.hide{
			display: none;
		}
		&__main{
			margin: 70px 40px 0;
			border-radius: 10px;
			background: white;
			text-align: center;
			padding: 20px 0;
			&-text{
				font-size: 16px;
				color: #030303;
				margin-bottom: 10px;
			}
			&-logo{
				width: 30px;
				height: 30px;
				margin: 0 auto 5px;
				background-image: url('../images/fun.png');
			}
			&-button{
				margin: 2px 40px 5px;
			}
		}
		&__close{
			text-align: center;
			&-icon{
				color: white;
				margin-top: 12px;
			}
		}
	}
}
.shade-enter{
	opacity: 0;
}
.shade-enter-active{
	opacity: 1;
	transition: all 300ms ease-in-out;
}
.shade-exit{
	opacity: 1;
}
.shade-exit-active{
	opacity: 0;
	transition: all 300ms ease-in-out;
}

.main-enter{
	opacity: 0;
	transform: translateY(-100%);
}
.main-enter-active{
	transform: translateY(0);
	opacity: 1;
	transition: all 300ms ease-in-out;
}
.main-exit{
	opacity: 1;
}
.main-exit-active{
	opacity: 0;
	transform: translateY(-100%);
	transition: all 300ms ease-in-out;
}




